import React from 'react';
import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';
import RaisedButton from 'material-ui/RaisedButton';


export default class TickerCard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      expanded: false, loaded:false, playing:false
    };
    
    this.data = []
    
    this.prodURL = "https://hidden-island-42423.herokuapp.com/api/"+props.ticker+"&x="+props.x+"&p=15&i="+props.inter+"&p=5d&f=o"
    this.devURL = "http://localhost:3003/api/"+props.ticker+"&x="+props.x+"&p=15&i="+props.inter+"&p=5d&f=o"

    fetch(this.prodURL, {
        method: "GET",
        mode: 'cors',
        headers:{
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Credentials':true,
            'Access-Control-Allow-Methods': 'GET'
            }
    }).then(response => { return response.text() }).then( response=>{
        let lines = response.split('\n')
        lines.splice(0, 8)
        lines.forEach(line => {
            this.data.push(line)
        })
        
        this.generateNotes(this.data)
        this.setState({loaded:true})
    })  
    
  }

    generateNotes(data) {
        data.pop()
        this.open = data.splice(0,1)
        this.notes = []

        data.forEach(val => {
            this.notes.push(Math.floor(((val-this.open) / this.open) * 1000))
        });
    }

  render() {
    return (
    <Card>
        <CardHeader
            title={this.props.ticker}
            subtitle={this.props.x}
            actAsExpander={true}
            showExpandableButton={true}
        />
        <CardActions>
            <RaisedButton label="Play" onClick={() => this.props.play(this.notes)} />
            <RaisedButton label="Stop" onClick={() =>this.props.stop()}/>
        </CardActions>
        <CardText expandable={true}>
        </CardText>
    </Card>
    );
  }
}

function createCORSRequest(method, url) {
    var xhr = new XMLHttpRequest();
    if ("withCredentials" in xhr) {
  
      // Check if the XMLHttpRequest object has a "withCredentials" property.
      // "withCredentials" only exists on XMLHTTPRequest2 objects.
      xhr.open(method, url, true);
  
    } else if (typeof XDomainRequest != "undefined") {
  
      // Otherwise, check if XDomainRequest.
      // XDomainRequest only exists in IE, and is IE's way of making CORS requests.
      xhr = new XDomainRequest();
      xhr.open(method, url);
  
    } else {
  
      // Otherwise, CORS is not supported by the browser.
      xhr = null;
  
    }
    return xhr;
  }
  



