import React, { Component } from 'react';
import './App.css';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TickerCard from "./tickercard"
import NotePlayer from './music'
import Header from "./header"
import AutoCompleteExampleSimple from "./searchbar"

let featuredStocks = [
  {
      name: "Apple",
      ticker: "AAPL",
      exchange: "NASDAQ"
  },
  {
      name: "Micron",
      ticker: "MU",
      exchange: "NASDAQ"
  },
  {
      name: "AMD",
      ticker: "AMD",
      exchange: "NASDAQ"
  },
]

class App extends Component {
  constructor(props){
    super(props)
    this.soundPlayer = new NotePlayer()

    this.state = {tickers: featuredStocks}
  }

  addTicker = (ticker, exchange) => {
    this.setState({tickers:  [{name: ticker, ticker, exchange}, ...this.state.tickers]})
  }

  render() {
    return (
      <MuiThemeProvider>
        <div>
          <Header></Header>
          <AutoCompleteExampleSimple addTicker={this.addTicker}></AutoCompleteExampleSimple>
          <div className="tickerlist">
            {this.state.tickers.map((stock)=> 
              <TickerCard 
                key={stock.ticker} 
                ticker={stock.ticker} 
                x={stock.exchange} 
                play={(buffer) => this.soundPlayer.play(buffer)} 
                stop={() => this.soundPlayer.stop()}
                inter="300"></TickerCard>
            )}
            </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;




