import React, {Component} from 'react';
import AutoComplete from 'material-ui/AutoComplete';
import RaisedButton from 'material-ui/RaisedButton/RaisedButton';

export default class AutoCompleteExampleSimple extends Component {
  state = {
    dataSource: [],
    exchange: "NASDAQ"
  };

  componentDidMount(){
    fetch("tickers.json").then((response) => {
    return response.json() }).then((response) =>{
        this.tickers = response
         
        this.setState({dataSource: response["NASDAQ"]})
    })
  }

  handleNewRequest = (value) => {
      this.props.addTicker(value, this.state.exchange)
  }

  setExchange = (value) => {
    this.setState({exchange:value, dataSource: this.tickers[value]})
  }
  render() {
    return (
      <div className="searchbar-wrapper">
        <AutoComplete
          className="searchbar"
          hintText="Search ticker (eg AAPL)"
          filter={AutoComplete.fuzzyFilter}
          dataSource={this.state.dataSource}
          onUpdateInput={this.handleUpdateInput}
          maxSearchResults={5}
          onNewRequest={this.handleNewRequest}
        />

        <ButtonGroup setExchange={this.setExchange}></ButtonGroup>
      </div>
    );
  }
}

class ButtonGroup extends React.Component {

    constructor(props){
        super(props)
        this.state = {active: 0}
    }
    handleClick = (e) => {
        switch(e.target.innerHTML){
            case "NASDAQ":
                this.setState({active: 0})
                break
            case "AMEX":
                this.setState({active: 2})            
                break
            case "NYSE":
                this.setState({active: 1})        
                break;
            }
    
        this.props.setExchange(e.target.innerHTML)
    }

    render(){
        return(
            <div>                
                <RaisedButton onClick={this.handleClick} primary={this.state.active === 0}>NASDAQ</RaisedButton>
                <RaisedButton onClick={this.handleClick} primary={this.state.active === 1}>NYSE</RaisedButton>
                <RaisedButton onClick={this.handleClick} primary={this.state.active === 2}>AMEX</RaisedButton>      
            </div>
        )
    }
}


